<template>
    <div>
      <!--  -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
      <!--  -->
      <el-form :inline="true" :model="formInline" class="user-search">
        <el-form-item label="">
          <el-autocomplete size="small" v-model="formInline.projectName" placeholder="" @select="handleSelect" :fetch-suggestions="querySearchAsync" @input="inputChange"></el-autocomplete>
        </el-form-item>
        <el-form-item label="">
          <el-select size="small" v-model="value" clearable placeholder="" @change="handleChange">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button size="small" type="primary" icon="el-icon-search" @click="search"></el-button>
          <el-button size="small" type="primary" icon="el-icon-plus" @click="handleEdit()"></el-button>
        </el-form-item>
      </el-form>
      <!---->
      <el-table size="small" :data="listData" highlight-current-row v-loading="loading" border element-loading-text="" style="width: 100%;">
        <el-table-column align="center" type="selection" width="60">
        </el-table-column>
        <el-table-column prop="id" label="id" v-if=false>
        </el-table-column>
        <el-table-column prop="projectEnvironment_id" label="id" v-if=false>
        </el-table-column>
        <el-table-column sortable prop="module_name" label="" width="300">
        </el-table-column>
        <el-table-column sortable prop="module_description" label="" width="300">
        </el-table-column>
        <el-table-column sortable prop="create_time" label="" width="300">
        </el-table-column>
        <el-table-column align="center" label="" min-width="300">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
            <el-button size="mini" type="danger" @click="apiModuleDelete(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <Pagination v-bind:child-msg="pageparm" @callFather="callFather"></Pagination>
      <!--  -->
      <el-dialog :title="title" :visible.sync="editFormVisible" width="30%" @click="closeDialog">
        <el-form label-width="120px" :model="editForm" :rules="rules" ref="editForm">
          <el-form-item label="" prop="e_name">
            <el-input size="small" v-model="editForm.e_name" auto-complete="off" placeholder="" :disabled=disabled></el-input>
          </el-form-item>
          <el-form-item label="" prop="m_name">
            <el-input size="small" v-model="editForm.m_name" auto-complete="off" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="m_description">
            <el-input type="textarea" size="small" v-model="editForm.m_description" auto-complete="off" placeholder=""></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button size="small" @click="closeDialog"></el-button>
          <el-button size="small" type="primary" :loading="loading" class="title" @click="submitForm('editForm')"></el-button>
        </div>
      </el-dialog>
    </div>
</template>

<script>
import { getAllProject } from '../../api/projectApi'
import { getAllProjectEnvironment, apiModuleList, saveApiModule, deleteApiModule } from '../../api/apiTestApi'
import Pagination from '../../components/Pagination'
export default {
  data() {
    return {
      nshow: true, //switch
      fshow: false, //switch
      loading: false, //
      editFormVisible: false, //
      title: '',
      disabled: false,
      editForm: {
        id: '',
        e_id: '',
        e_name: '',
        m_name: '',
        m_description: '',
        create_time: '',
        token: localStorage.getItem('logintoken')
      },
      // rules
      rules: {
        e_name: [{ required: true, message: '', trigger: 'blur' }],
        m_name: [{ required: true, message: '', trigger: 'blur' }],
        m_description: [{ required: true, message: 'url', trigger: 'blur' }]
      },
      projects: '',
      state: '',
      formInline: {
        page: 1,
        limit: 10,
        varLable: '',
        varName: '',
        projectName:'',
        projectEnvironment_id: '',
        projectEnvironment_name: '',
        token: localStorage.getItem('logintoken')
      },
      // 
      seletedata: {
        ids: '',
        token: localStorage.getItem('logintoken')
      },
      userparm: [], //
      listData: [], //
      // 
      pageparm: {
        currentPage: 1,
        pageSize: 10,
        oldPageSize: 10,
        total: 10
      },
      // 
      options: '',
      value: ''
    }
  },
  // 
  components: {
    Pagination
  },
  /**
   * 
   */

  /**
   * 
   */
  created() {
    this.loadAllProject();
  },
  // mounted(){
  //   this.projects = this.getAllProject;
  // },

  /**
   * 
   */
  methods: {
    // 
    getdata(parameter) {
      this.loading = true;
      this.pageparm.currentPage = this.formInline.page;
      this.pageparm.pageSize = this.formInline.limit;
      parameter = {
        currentPage: this.pageparm.currentPage,
        pageSize: this.pageparm.pageSize,
        projectEnvironment_id : this.formInline.projectEnvironment_id
      };
      /***
       *  
       */
      apiModuleList(parameter)
        .then(res => {
          this.loading = false;
          if (res.success === false) {
            this.$message({
              type: 'info',
              message: res.msg
            })
          } else {
            this.listData = res.data;
            // 
            // this.pageparm.currentPage = this.formInline.page;
            // this.pageparm.pageSize = this.formInline.limit;
            this.pageparm.total = res.count;
          }
        })
        .catch(err => {
          this.loading = false;
          console.log(err);
          this.$message.error('')
        })
    },
    // 
    callFather(parm) {
      if(this. pageparm.pageSize !== parm.pageSize){
        this.formInline.page = 1;
        this.formInline.limit = parm.pageSize;
        this.getdata(this.formInline)
      }else {
        this.formInline.page = parm.currentPage;
        this.formInline.limit = parm.pageSize;
        this.getdata(this.formInline)
      }
    },
    // 
    search() {
      this.formInline.page = 1;
      this.getdata(this.formInline)
    },
    //
    handleEdit: function(index, row) {
      this.editFormVisible = true;
      if (row != undefined && row != 'undefined') {
        this.title = '';
        this.disabled = true;
        this.editForm.id = row.id;
        this.editForm.e_id = this.formInline.projectEnvironment_id;
        this.editForm.e_name = this.formInline.projectEnvironment_name;
        this.editForm.m_name = row.module_name;
        this.editForm.m_description = row.module_description;
      } else {
        this.title = '';
        this.disabled = true;
        this.editForm.id = '';
        this.editForm.e_id = this.formInline.projectEnvironment_id;
        this.editForm.e_name = this.formInline.projectEnvironment_name;
        this.editForm.m_name = '';
        this.editForm.m_description = '';
      }
    },
    // 
    closeDialog() {
      this.editFormVisible = false
    },
    // 
    submitForm(editData) {
      this.$refs[editData].validate(valid => {
        if (valid) {
          saveApiModule(this.editForm)
            .then(res => {
              this.editFormVisible = false;
              this.loading = false;
              if (res.success) {
                this.getdata(this.formInline);
                this.$message({
                  type: 'success',
                  message: res.msg
                })
              } else {
                this.$message({
                  type: 'info',
                  message: res.msg
                })
              }
            })
            .catch(err => {
              this.editFormVisible = false;
              this.loading = false;
              this.$message.error('')
            })
        } else {
          return false
        }
      })
    },
    // api
    apiModuleDelete(index, row) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          let parameter = {
            id: row.id
          };
          deleteApiModule(parameter)
            .then(res => {
              if (res.success) {
                this.$message({
                  type: 'success',
                  message: res.msg
                });
                this.getdata(this.formInline)
              } else {
                this.$message({
                  type: 'info',
                  message: res.msg
                })
              }
            })
            .catch(err => {
              console.log(err);
              this.loading = false;
              this.$message.error('')
            })
        })
        .catch((err) => {
          console.log(err)
        })
    },
    // 
    loadAllProject(){
      getAllProject().then(
        res => {
          this.projects = res;
        }
      )
    },
    querySearchAsync(queryString, cb) {
      var projects = this.projects;
      var results = queryString ? projects.filter(this.createStateFilter(queryString)) : projects;

      cb(results);
    },
    createStateFilter(queryString) {
      return (state) => {
        return (state.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
      };
    },
    handleSelect(item) {
      this.loadAllProjectEnvironment(item);
    },
    // 
    loadAllProjectEnvironment(item){
      var params = {
        name: item.value
      };
      getAllProjectEnvironment(params).then(
        res => {
          this.options = res;
        }
      )
    },
    // api
    handleChange(val){
      this.formInline.projectEnvironment_id = val;
      let obj = {};
      obj = this.options.find((item) => {
        return item.value === val;
      });
      this.formInline.projectEnvironment_name = obj.label;
      this.getdata(this.formInline);
    },
    //
    inputChange(){
      this.formInline.projectEnvironment_id = '';
      this.formInline.projectEnvironment_name = '';
      this.options = '';
      this.value = '';
    }
  }
}
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
/deep/ textarea {
  font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","",Arial,sans-serif !important;
}
</style>



