// 
import Vue from 'vue';
import Router from 'vue-router';
// 
import login from '@/views/login';
// 
import index from '@/views/index';
/**
 * 
 */
// 
import user from '@/views/system/user';
// 
import Module from '@/views/system/Module';
// 
import Role from '@/views/system/Role';
// 
import Dept from '@/views/system/Dept';
// 
import Variable from '@/views/system/Variable';
// 
import Permission from '@/views/system/Permission';
/**
 * 
 */

// 
import statistics from '@/views/charts/statistics';

/**
 * 
 */
// 
import project from '@/views/project/project';
// 
import projectEnvironment from '@/views/project/projectEnvironment';

/**
 * 
 */
// 
import apiModule from '@/views/apiTest/apiModule';
// 
import apiManagement from "@/views/apiTest/apiManagement";
// 
import apiTestCase from "@/views/apiTest/apiTestCase";
// 
import workbench from "@/views/apiTest/workbench";
// 
import apiTestTask from "@/views/apiTest/apiTestTask";
// 
import apiTestReport from "@/views/apiTest/apiTestReport";

// 
Vue.use(Router);

// 
export default new Router({
    routes: [{
        path: '/',
        name: '',
        component: login,
        hidden: true,
        meta: {
                requireAuth: false,
                keepAlive: false
        }
    }, {
        path: '/login',
        name: '',
        component: login,
        hidden: true,
        meta: {
                requireAuth: false,
                keepAlive: false
        }
    }, {
        path: '/index',
        name: '',
        component: index,
        iconCls: 'el-icon-tickets',
        children: [ {
            path: '/system/user',
            name: '',
            component: user,
            meta: {
                requireAuth: true,
                keepAlive: false
            }
        }, {
            path: '/system/Module',
            name: '',
            component: Module,
            meta: {
                requireAuth: true,
                keepAlive: false
            }
        }, {
            path: '/system/Role',
            name: '',
            component: Role,
            meta: {
                requireAuth: true,
                keepAlive: false
            }
        }, {
            path: '/system/Dept',
            name: '',
            component: Dept,
            meta: {
                requireAuth: true,
                keepAlive: false
            }
        }, {
            path: '/system/Variable',
            name: '',
            component: Variable,
            meta: {
                requireAuth: true,
                keepAlive: false
            }
        }, {
            path: '/system/Permission',
            name: '',
            component: Permission,
            meta: {
                requireAuth: true,
                keepAlive: false
            }
        }, {
            path: '/charts/statistics',
            name: '',
            component: statistics,
            meta: {
                requireAuth: true,
                keepAlive: false
            }
        }, {
            path: '/project/project',
            name: '',
            component: project,
            meta: {
                requireAuth: true,
                keepAlive: false
            }
        }, {
            path: '/project/projectEnvironment',
            name: '',
            component: projectEnvironment,
            meta: {
                requireAuth: true,
                keepAlive: false
            }
        }, {
            path: '/apiTest/apiModule',
            name: '',
            component: apiModule,
            meta: {
                requireAuth: true,
                keepAlive: false
            }
        }, {
            path: '/apiTest/apiManagement',
            name: '',
            component: apiManagement,
            meta: {
                requireAuth: true,
                keepAlive: true
            }
        }, {
            path: '/apiTest/apiManagement/apiTestCase',
            name: '',
            component: apiTestCase,
            meta: {
                requireAuth: true,
                keepAlive: false
            }
        }, {
            path: '/apiTest/apiManagement/apiTestCase/workbench',
            name: '',
            component: workbench,
            meta: {
                requireAuth: true,
                keepAlive: false
            }
        }, {
            path: '/apiTest/apiTestTask',
            name: '',
            component: apiTestTask,
            meta: {
                requireAuth: true,
                keepAlive: false
            }
        }, {
            path: '/apiTest/apiTestTask/apiTestReport',
            name: '',
            component: apiTestReport,
            meta: {
                requireAuth: true,
                keepAlive: false
            }
        }]
    }]
})



